import { HamburgerIcon } from "@chakra-ui/icons";
import {
  Button,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
  Text,
  useDisclosure,
} from "@chakra-ui/react";
import React from "react";
function NavbarDrawer() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  return (
    <>
      <Button ref={btnRef} onClick={onOpen}>
        <HamburgerIcon />
      </Button>
      <Drawer
        isOpen={isOpen}
        placement="top"
        onClose={onClose}
        finalFocusRef={btnRef}
        className="drawer"
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          <DrawerHeader></DrawerHeader>

          <DrawerBody fontWeight={"bold"} fontSize='lg' lineHeight={'30px'}>
            <Text >Home</Text>
            <Text>About Me</Text>
            <Text>Skills</Text>
            <Text>Projects</Text>
            <Text>Contact</Text>
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </>
  );
}
export default NavbarDrawer;
